@import url("https://fonts.googleapis.com/css?family=Space+Mono");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 10px;
  vertical-align: baseline;
  font-family: 'Space Mono', monospace; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

*, *:before, *:after {
  box-sizing: border-box; }

.footer__container {
  width: 100%;
  font-size: 1.6rem;
  line-height: 2.2rem;
  text-align: center;
  border-top: 0.2rem solid #666; }

.header__container {
  display: flex;
  flex-flow: row;
  justify-content: space-between;
  position: relative;
  padding: 0.8rem 0.4rem;
  border-bottom: 0.2rem solid #666; }

.header__links {
  display: none;
  position: absolute;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  border-bottom: 0.2rem solid #666;
  width: 100%;
  bottom: -0.2rem;
  left: 0;
  transform: translateY(100%);
  background: #fff; }
  .header__links.active {
    display: flex; }

.header__toggle {
  font-size: 2.2rem; }

.header__logo {
  font-size: 2.2rem;
  text-decoration: none;
  color: inherit; }

.header__link {
  line-height: 2.2rem;
  font-size: 2.2rem;
  padding: 0.8rem;
  text-decoration: none;
  color: inherit; }

@media (min-width: 415px) {
  .header__toggle {
    display: none; }
  .header__links {
    display: flex;
    flex-flow: row;
    position: static;
    border: 0;
    transform: none;
    width: auto; }
  .header__link {
    padding: 0 0.8rem; } }

.site__container {
  max-width: 1200px;
  width: 100vw;
  display: flex;
  flex-flow: column;
  min-height: 100vh;
  margin: auto; }

.site__content {
  flex-grow: 2; }

.home__container {
  display: flex;
  flex-flow: column;
  padding: 0.8rem; }

.home__info {
  padding: 0.4rem 0;
  font-size: 1.6rem; }

.home__link {
  font-size: 1.6rem;
  color: inherit;
  text-decoration: none; }
  .home__link:hover {
    text-decoration: underline; }

.bikes__container {
  display: flex;
  flex-flow: column; }

.bike__container {
  display: flex;
  flex-flow: column;
  border-bottom: 0.2rem solid #666;
  padding: 0.8rem 0.4rem; }
  .bike__container:last-child {
    border: 0; }

.bike__image {
  height: auto;
  max-width: 100%;
  padding: 1.2rem 0; }

.bike__name {
  font-size: 2.2rem; }

.bike__info {
  font-size: 1.6rem; }

@media (min-width: 415px) {
  .bike__name {
    order: 1; }
  .bike__info {
    order: 2; }
  .bike__image {
    order: 3; } }

